
import { DOCUMENT } from '@angular/common';
import { Inject, Injectable } from '@angular/core';

import Driver from 'driver.js';
/*
 * https://madewith.cn/766
 * 
 * */
@Injectable({
  providedIn: 'root'
})
export class DriverService {
  constructor(@Inject(DOCUMENT) private doc: Document) {}

  load(): void {
    setTimeout(() => {
      const driver = new Driver({
        animate: false,
        allowClose: true,
        doneBtnText: 'Kt thc',
        closeBtnText: 'Khp kn',
        nextBtnText: 'Bc tip theo',
        prevBtnText: 'Trc',
        onHighlightStarted: () => {
          this.doc.body.style.cssText = 'overflow:hidden';
        },
        onReset: () => {
          this.doc.body.style.cssText = '';
        }
      });
      driver.defineSteps([
        {
          element: '#menuNav',
          popover: {
            title: 'Menu',
            description: 'y l menu',
            position: 'right-center'
          }
        },
        {
          element: '#drawer-handle',
          popover: {
            title: 'Nt ci t ch ',
            description: 'Nhp  m rng ch , bn c th ko n ln v xung',
            position: 'left'
          }
        },
        {
          element: '#tools',
          popover: {
            title: 'Thanh cng c',
            description: 'Mn hnh kha, Menu tm kim, Ton mn hnh, Tin nhn thng bo, ng xut, a ngn ng',
            position: 'bottom'
          }
        },
        {
          element: '#chats',
          popover: {
            title: 'Lin h vi qun tr vin',
            description: 'Lin h vi qun tr vin',
            position: 'top'
          }
        },
        {
          element: '#trigger',
          popover: {
            title: 'Thu gn menu',
            description: 'thu gn menu',
            position: 'bottom'
          }
        },
        {
          element: '#multi-tab',
          popover: {
            title: 'Nhiu nhn',
            description: 'Nhp chut phi vo mt tab  m rng nhiu ty chn v cun con ln chut  cun cc tab sau khi vt ra ngoi mn hnh.',
            position: 'bottom'
          }
        },
        {
          element: '#multi-tab2',
          popover: {
            title: 'nhiu nhn',
            description: 'Nhp chut phi vo mt tab  m rng nhiu ty chn v cun con ln chut  cun cc tab sau khi vt ra ngoi mn hnh.',
            position: 'bottom'
          }
        }
      ]);
      driver.start();
    }, 500);
  }
}
